@font-face {
  font-family: "DMSans";
  src: url("../fonts/DMSans-Regular.eot");
  src: local("☺"), url("../fonts/DMSans-Regular.woff") format("woff"),
    url("../fonts/DMSans-Regular.ttf") format("truetype"),
    url("../fonts/DMSans-Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
